<template>
    <div class="container">
        <div class="container-title">网格职责</div>
        <div class="table-list">
            <el-table ref="singleTable" :data="tableData" style="width: 100%">
                <el-table-column prop="name" label="网格员名称">
                </el-table-column>
                <el-table-column prop="duty" label="网格员职责">
                </el-table-column>
                <el-table-column fixed="right" label="操作" width="120">
                    <template slot-scope="scope">
                        <el-button @click.native.prevent="editDuty(scope.$index)" type="text" size="small">
                            编辑
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        <el-dialog title="职责" :visible.sync="dialogVisible">
            <el-input type="textarea" :autosize="{ minRows: 6, maxRows: 10}" placeholder="请输入内容" v-model="textarea">
            </el-input>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="ok">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>


<script>
export default {
    data () {
        return {
            tableData: [
                // {
                //     name: '总网格长',
                //     duty: '负责总xxxx',
                // },
                // {
                //     name: '副网格长',
                //     duty: '负责副xxxx',
                // }
            ],
            dialogVisible: false,
            textarea: ''
        };
    },
    created () {
        this.$message.error('调试中');
    },
    methods: {
        editDuty (index) {
            this.selectItem = this.tableData[index];
            this.textarea = this.selectItem.duty;
            this.dialogVisible = true;
            this.index = index;
        },
        ok () {
            this.tableData[this.index].duty = this.textarea;
            this.dialogVisible = false;
        }
    }
}
</script>

<style scoped>
.list-img {
    width: 200px;
    cursor: pointer;
}
.previewImg-box {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}
.previewImg {
    width: 500px;
}
</style>